import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import { DateTime } from "luxon";

i18n
  // detect user language
  // learn more: https://github.com/i18next/i18next-browser-languageDetector
  .use(LanguageDetector)
  // pass the i18n instance to react-i18next.
  .use(initReactI18next)
  // init i18next
  // for all options read: https://www.i18next.com/overview/configuration-options
  .init({
    debug: true,
    fallbackLng: "en",
    interpolation: {
      escapeValue: false, // not needed for react as it escapes by default
    },
    resources: {
      en: {
        translation: {
          header: {
            appName: "ToDoList App",
            date: "Today is {{date, DATE_SHORT}}",
          },
          itemList: {
            notResolvedOnly: "not resolved only",
            allItems: "all items",
            counter_zero: "No items to show",
            counter_one: "Only one item to show",
            counter_other: "Showing {{count}} items",
          },
        },
      },
      cs: {
        translation: {
          header: {
            appName: "Aplikace kolovnk",
            date: "Dnes je {{date, DATE_SHORT}}",
          },
          itemList: {
            notResolvedOnly: "pouze nevyeen",
            allItems: "vechny poloky",
            counter_zero: "dn poloka k zobrazen",
            counter_one: "Zobrazuji pouze jednu poloku",
            counter_few: "Zobrazuji {{count}} poloky",
            counter_other: "Zobrazuji {{count}} poloek",
          },
        },
      },
    },
  });

// new usage
i18n.services.formatter.add("DATE_SHORT", (value, lng, options) => {
  return DateTime.fromJSDate(value).setLocale(lng).toLocaleString(DateTime.DATE_SHORT);
});

i18n.services.formatter.add("DATE_HUGE", (value, lng, options) => {
  return DateTime.fromJSDate(value).setLocale(lng).toLocaleString(DateTime.DATE_HUGE);
});

export default i18n;
